import React from 'react';

import logo from '../../resources/logo128.png';

export default () => {
  return (
    <div className='content content-narrow'>
      <h1>About</h1>
      <br />
      <p>Thoracle is a tool to turn the non-interactive tweet screenshots into real tweets, which can be liked, shared and mentioned in a more convenient way.</p>
      <p>The main goal of the Thoracle project is preventing public manipulation by fake news through social media. It has been developed as a senior-year project by students of Bilkent University.</p>
      <br />
      <div className='footer-about'>
        <img alt='Thoracle' src={logo} width='64' height='64'/>
        <p><i>Thoracle ~ 2020</i></p>
      </div>
    </div>
  );
}
