import React, {  Component } from 'react';
import { withRouter} from 'react-router-dom';
import queryString from 'query-string';
import { TwitterTweetEmbed } from 'react-twitter-embed';

import Botometer from './Botometer';
import RelevantNews from './RelevantNews';
import Comments from './Comments';
import { getTweetId } from '../../../utils/Commons';
import { LoadState } from '../../../utils/Enums';
import { getTweetDataUrl } from '../../../utils/Urls';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

class TweetPage extends Component {
  state = {
    loadState: LoadState.LOADING,
    comments: [],
    votes: {}
  };

  constructor(props) {
    super(props);
    const paramsString = props.location.search;
    const params = queryString.parse(paramsString);
    this.tweetUrl = params.tweetUrl;
    this.tweetId = getTweetId(params.tweetUrl);
    this.showFoundMessage = (params.found === 'true');
  }

  async componentDidMount() {
    try {
      const response = await fetch(`${getTweetDataUrl}?tweetUrl=${this.tweetUrl}`);
      const responseJson = await response.json();
      const votes = {
        upvotes: responseJson.userRatings.filter(rating => rating.upvotes === true).length,
        downvotes: responseJson.userRatings.filter(rating => rating.upvotes === false).length,
      }
      this.setState({
        loadState: LoadState.LOADED,
        comments: responseJson.posts,
        votes
      });
    }
    catch (error) {
      console.error(error);
      this.setState({
        loadState: LoadState.FAILED,
        comments: [],
        votes: {}
      });
    }
  }

  renderFoundMessage() {
    if (this.showFoundMessage) {
      return (<h2><span role="img" aria-label="found">✔</span> Tweet found!</h2>);
    }
    return (<h2><span role="img" aria-label="analysis">📃</span> Tweet Analysis</h2>);
  }

  render() {
    return (
      <div className='content'>
        {this.renderFoundMessage()}
        <hr />
        <Row>
          <Col xs="12">
            <TwitterTweetEmbed
                tweetId={this.tweetId}
                options={{conversation: 'none'}} />
          </Col>
        </Row>
          {/*</Col>*/}
        <hr />
        <Botometer tweetUrl={this.tweetUrl} />
        <hr />
        {/*<Voting loadState={this.state.loadState} votes={this.state.votes} />*/}
        {/*<hr />*/}
        <Comments pageUrl={window.location.href} disqusId={this.tweetId} />
        <hr />
        <RelevantNews tweetUrl={this.tweetUrl} />
        <hr />
      </div>
    );
  }
}

export default withRouter(TweetPage);
