import React, {Component, Fragment} from 'react';
import {Link} from 'react-router-dom';
import {LinkContainer} from 'react-router-bootstrap';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button';
import {redirectNewTab} from '../utils/Commons';
import {extensionUrl} from '../utils/Urls';

import logo from '../resources/logo128.png';
import {getSession} from '../utils/Session';

export default class AppNavbar extends Component {
  redirectToExtension() {
    redirectNewTab(extensionUrl);
  }

  componentDidMount() {
    this.setState({ session: getSession() });
  }

  renderBrand() {
    return (
      <Navbar.Brand as='a' href='/'>
        <img alt='Thoracle' src={logo} width='32' height='32' className='d-inline-block align-top'/>
        &nbsp;
        Thoracle
      </Navbar.Brand>
    );
  }

  renderExtensionButton() {
    return (
      <Nav.Item>
        <Button onClick={() => this.redirectToExtension()} variant='warning'>
          Get the Chrome extension!
        </Button>
      </Nav.Item>
    );
  }

  renderNavLink(path, title) {
    return (
      <LinkContainer to={path}>
        <Nav.Link as={Link} to={path}>{title}</Nav.Link>
      </LinkContainer>
    );
  }

  renderNavLinks() {
    return (
      <Fragment>
        {this.renderNavLink('/about', 'About')}
      </Fragment>
    );
  }

  render() {
    return (
      <Navbar fixed="top" collapseOnSelect expand='lg' bg='dark' variant='dark'>
        {this.renderBrand()}
        <Navbar.Toggle aria-controls='responsive-navbar-nav' />
        <Navbar.Collapse id='responsive-navbar-nav'>
          <Nav className='mr-auto'/>
          <Nav className='navbar-elements'>
            {this.renderNavLinks()}
            {this.renderExtensionButton()}
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}
